// all inline styles
export const styles = {
    container: {
        marginTop: "5vh",
        marginBottom: "3vh",
        marginLeft: "50px",
        marginRight: "50px",
        textAlign: "left",
    },
    headerImg: {
        height: "25vh",
        width: "100vw",
        minHeight: "100px",
        marginTop: "20px",
        marginLeft: "3vw",
        textAlign: "center",
    },
    headerImgDiv: {
        textAlign: "center",
    },
    formWrap: {
        padding: "40px",
        borderRadius: "20px",
    },
    boxHeader: {
        textAlign: "center",
        fontSize: "3em",
        paddingBottom: "20px",
    },
    bottom: {
        marginTop: "40px",
        marginBottom: "40px",
        textAlign: "center",
        justifyItems: "center",
    },
    links: {
        textDecoration: "none",
    },
    chatTitle: {
        fontSize: window.innerWidth > 900 ? "3em" : "1.5em",
        marginTop: "40px",
        marginBottom: "20px",
        paddingLeft: "20px",
        paddingRight: "20px",
    },
    chatWrap: {
        height: "70vh",
        overflowY: "auto",
        padding: "40px",
        borderRadius: "20px",
    },
    chatInput: {
        marginTop: "15px",
        borderRadius: "20px",
    },
    popup: {
        position: "fixed",
        right: "10px",
        bottom: "10px",
    },

    // profile image
    profileImage: {
        marginLeft: "4px",
        width: "32px",
        height: "32px",
        borderRadius: "32px",
    }
}